import React from "react";

const Page14 = React.forwardRef((props, ref) => {
  return (
    <div className="demoPage flip_book_right_background" ref={ref}>
      <div className="tokenomics_element">
        <div className="contact_us_canvas">
          <div style={{ padding: "0.5vh" }}>
            <input className="input_form_element" placeholder="Name"></input>
          </div>
          <div style={{ padding: "0.5vh" }}>
            <input className="input_form_element" placeholder="Email"></input>
          </div>
          <div style={{ padding: "0.5vh" }}>
            <textarea
              className="textarea_form_element"
              placeholder="Message"
              rows={"5"}
            />
          </div>
          <div
            className="submit_button"
            style={{ width: "100%", padding: "10px", backgroundColor: "white" }}
          >
            <a
              href="https://ikigaionsol.com/pfp.html"
              style={{ textDecoration: "none" }}
            >
              <span>Submit</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
});

export default Page14;
